import {AppBar, Box, Link, Toolbar, Typography} from "@material-ui/core";
import {useStyles} from "./MenuBar.styles";
import {useNavigate} from "react-router-dom";
import {userService} from "../../services/user.service";
import {observer} from "mobx-react-lite";
import {FC, Fragment} from "react";

type LinkComponentProps = {
    name: string
    path: string
}
const MenuBar = observer(() => {
    const classes = useStyles();
    const navigate = useNavigate();
    const {isAuth} = userService
    const handleNavigate = (path: string) => () => navigate(path)
    const LinkComponent: FC<LinkComponentProps> = ({path, name}) => (<Link
        className={classes.appBarLink}
        component="button"
        variant="body2"
        onClick={handleNavigate(path)}
    >
        {name}
    </Link>)
    return (
        <AppBar position="static" className={classes.root}>
            <Toolbar>
                <Box className={classes.leftSide}>
                    <Typography variant="h6" className={classes.title} onClick={() => navigate('/')}>
                         
                    </Typography>
                    <Box className={classes.items}>
                        <LinkComponent path={'/universities'} name={''}/>
                        <LinkComponent path={'/test'} name={''}/>
                        {!isAuth
                            ? (<Fragment>
                                    <LinkComponent path={'/login'} name={''}/>
                                    <LinkComponent path={'/registration'} name={''}/>
                                </Fragment>
                            )
                            : (
                                <Fragment>
                                    <LinkComponent path={'/statics'} name={' '}/>
                                    <LinkComponent path={'/profile'} name={' '}/>
                                    <LinkComponent path={'/logout'} name={''}/>
                                </Fragment>
                            )}

                    </Box>
                </Box>

                {/*<IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">*/}
                {/*    <MenuIcon/>*/}
                {/*</IconButton>*/}
            </Toolbar>
        </AppBar>
    )
})
export default MenuBar
