import {
    Box,
    Button,
    Card,
    CardContent,
    FormControlLabel,
    Radio,
    RadioGroup,
    TextField,
    Typography
} from "@material-ui/core";
import {useStyles} from "./Registration.styles"
import {ChangeEvent, useState} from "react";
import {observer} from "mobx-react-lite";
import {userService} from "../../services/user.service";
import {useGlobalStyles} from "../../assets/Global.styles";


const Registration = observer(() => {
    const classes = useStyles()
    const globalClasses = useGlobalStyles()
    const [values, setValues] = useState({
        login: '',
        firstName: '',
        secondName: '',
        email: '',
        birthdate: '',
        isMan: '',
        password: ''
    })
    const handleChange = ({currentTarget: {name, value}}: ChangeEvent<HTMLInputElement>) => setValues({
        ...values,
        [name]: value
    })

    const handleSubmit = () => userService.registration(values).catch(console.log)

    return (<Box className={classes.root}>
        <Typography variant={'h5'} className={classes.title}></Typography>
        <Card className={classes.cardContainer}>
            <CardContent className={classes.registerCard}>
                <TextField id="outlined-basic"
                           label=""
                           name={'login'}
                           value={values.login}
                           onChange={handleChange}
                           size={'small'}
                           variant="outlined"
                           className={globalClasses.input}/>
                <TextField id="outlined-basic"
                           label=""
                           name={'firstName'}
                           value={values.firstName}
                           onChange={handleChange}
                           size={'small'}
                           variant="outlined"
                           className={globalClasses.input}/>
                <TextField id="outlined-basic"
                           label=""
                           name={'secondName'}
                           value={values.secondName}
                           onChange={handleChange}
                           size={'small'} variant="outlined"
                           className={globalClasses.input}/>
                <TextField id="outlined-basic"
                           label=""
                           name={'email'}
                           value={values.email}
                           onChange={handleChange}
                           size={'small'}
                           variant="outlined"
                           className={globalClasses.input}/>
                <TextField id="outlined-basic"
                           type={'date'}
                           name={'birthdate'}
                           value={values.birthdate}
                           onChange={handleChange}
                           size={'small'} variant="outlined"
                           className={globalClasses.input}/>
                <RadioGroup aria-label="gender"
                            name={'isMan'}
                            value={values.isMan}
                            onChange={handleChange}
                            className={globalClasses.sexRadio}>
                    <FormControlLabel value="male"
                                      control={<Radio color="primary" size={'small'}/>}
                                      label="."/>
                    <FormControlLabel value="female"
                                      control={<Radio color="primary" size={'small'}/>}
                                      label="."/>
                </RadioGroup>
                <TextField id="outlined-basic"
                           label=""
                           name={'password'}
                           value={values.password}
                           onChange={handleChange}
                           type={'password'}
                           size={'small'}
                           variant="outlined"
                           className={globalClasses.input}/>
                <Button variant={'contained'}
                        color={'primary'}
                        className={globalClasses.button}
                        onClick={handleSubmit}
                        fullWidth></Button>
            </CardContent>
        </Card>
    </Box>)
})
export default Registration
