import {observer} from "mobx-react-lite";
import {ChangeEvent, Fragment, useState} from "react";
import {useGlobalStyles} from "../../assets/Global.styles";
import {Box, Button, FormControlLabel, Paper, Radio, RadioGroup, TextField, Typography} from "@material-ui/core";

const EditProfile = observer(() => {
    const globalClasses = useGlobalStyles()
    const [values, setValues] = useState({
        login: '',
        firstName: '',
        secondName: '',
        email: '',
        birthdate: '',
        isMan: '',
        password: ''
    })
    const handleChange = ({currentTarget: {name, value}}: ChangeEvent<HTMLInputElement>) => setValues({
        ...values,
        [name]: value
    })
    const handleSubmit = () => console.log(values)
    return (
        <Fragment>
            <Typography className={globalClasses.title}>  </Typography>
            <Paper className={globalClasses.card}>
                <Box>
                    <Box className={globalClasses.label}></Box>
                    <Box><TextField id="outlined-basic"
                                    label=""
                                    fullWidth
                                    name={'firstName'}
                                    value={values.firstName}
                                    onChange={handleChange}
                                    size={'small'}
                                    variant="outlined"
                                    className={globalClasses.input}/></Box>
                </Box>
                <Box>
                    <Box className={globalClasses.label}></Box>
                    <Box> <TextField id="outlined-basic"
                                     label=""
                                     fullWidth
                                     name={'secondName'}
                                     value={values.secondName}
                                     onChange={handleChange}
                                     size={'small'} variant="outlined"
                                     className={globalClasses.input}/></Box>
                </Box>
                <Box>
                    <Box className={globalClasses.label}></Box>
                    <Box>
                        <TextField id="outlined-basic"
                                   label=""
                                   fullWidth
                                   name={'login'}
                                   value={values.login}
                                   onChange={handleChange}
                                   size={'small'}
                                   variant="outlined"
                                   className={globalClasses.input}/>
                    </Box>
                </Box>
                <Box>
                    <Box className={globalClasses.label}></Box>
                    <Box><TextField id="outlined-basic"
                                    label=""
                                    fullWidth
                                    name={'email'}
                                    value={values.email}
                                    onChange={handleChange}
                                    size={'small'}
                                    variant="outlined"
                                    className={globalClasses.input}/></Box>
                </Box>
                <Box>
                    <Box className={globalClasses.label}> </Box>
                    <Box><TextField id="outlined-basic"
                                    type={'date'}
                                    fullWidth
                                    name={'birthdate'}
                                    value={values.birthdate}
                                    onChange={handleChange}
                                    size={'small'} variant="outlined"
                                    className={globalClasses.input}/></Box>
                </Box>
                <Box>
                    <Box className={globalClasses.label}></Box>
                    <Box>
                        <RadioGroup aria-label="gender"
                                    name={'isMan'}
                                    value={values.isMan}
                                    onChange={handleChange}
                                    >
                            <FormControlLabel value="male"
                                              control={<Radio color="primary" size={'small'}/>}
                                              label=""/>
                            <FormControlLabel value="female"
                                              control={<Radio color="primary" size={'small'}/>}
                                              label=""/>
                        </RadioGroup>
                    </Box>
                </Box>
                <Box>
                    <Box className={globalClasses.label}></Box>
                    <Box>
                        <TextField id="outlined-basic"
                                   label=""
                                   fullWidth
                                   name={'password'}
                                   value={values.password}
                                   onChange={handleChange}
                                   type={'password'}
                                   size={'small'}
                                   variant="outlined"
                                   className={globalClasses.input}/>
                    </Box>
                </Box>
                <Box>
                    <Button variant={'contained'}
                            color={'primary'}
                            className={globalClasses.button}
                            onClick={handleSubmit}
                            fullWidth></Button>
                </Box>


            </Paper>
        </Fragment>
    )
})
export default EditProfile
